'use client';
import { useState } from "react";
import { getIndices, getVersions, getVersionContent, compare } from "./api/upload/api";

type Index = { id: string; name: string };
type Version = { id: string; version_name: string };

export default function Page() {
  const [indices, setIndices] = useState<Index[]>([]);
  const [selectedIndexId, setSelectedIndexId] = useState<string | null>(null);
  const [versions, setVersions] = useState<Version[]>([]);
  const [selectedVersionIds, setSelectedVersionIds] = useState<string[]>([]);
  const [generatedCode, setGeneratedCode] = useState<string | null>(null);

  const handleIndexSelect = async (id: string) => {
    setSelectedIndexId(id);
    const v = await getVersions(id);
    setVersions(v);
    setSelectedVersionIds([]); // reset selection
    setGeneratedCode(null);
  };

  const handleCompare = async () => {
    const [v1, v2] = await Promise.all([
      getVersionContent(selectedVersionIds[0]),
      getVersionContent(selectedVersionIds[1]),
    ]);
    const res = await compare(v1.content, v2.content);
    setGeneratedCode(res.code);
  };

  return (
    <div className="grid grid-cols-3 gap-4 p-4">
      {/* Panel Izquierdo */}
      <div>
        <h2>Índices</h2>
        <ul>
          {indices.map((i) => (
            <li key={i.id}>
              <button onClick={() => handleIndexSelect(i.id)}>{i.name}</button>
            </li>
          ))}
        </ul>
      </div>

      {/* Panel Central */}
      <div>
        <h2>Versiones</h2>
        <ul>
          {versions.map((v) => (
            <li key={v.id}>
              <label>
                <input
                  type="checkbox"
                  checked={selectedVersionIds.includes(v.id)}
                  onChange={() =>
                    setSelectedVersionIds((prev) =>
                      prev.includes(v.id)
                        ? prev.filter((x) => x !== v.id)
                        : [...prev, v.id].slice(-2)
                    )
                  }
                />
                {v.version_name}
              </label>
            </li>
          ))}
        </ul>
        <button
          disabled={selectedVersionIds.length !== 2}
          onClick={handleCompare}
        >
          Comparar y Generar Código
        </button>
      </div>

      {/* Panel Derecho */}
      <div>
        <h2>Resultado</h2>
        <pre className="bg-gray-100 p-2">{generatedCode}</pre>
      </div>
    </div>
  );
}
